<template>
 <div class="loginContainer">
    <form @submit.prevent="loginSubmit">
      <h4>Username</h4>
      <span class="p-input-icon-left">
        <i class="pi pi-user"></i>
        <InputText
          type="text"
          v-model="username"
          placeholder="Username"
        ></InputText>
      </span>
      <h4>Password</h4>
      <span class="p-input-icon-left">
        <i class="pi pi-lock"></i>
        <InputText
          type="password"
          v-model="password"
          placeholder="Password"
        ></InputText>
      </span>
      <br />
      <Button label="Submit" type="submit"  style="margin-top: 15px;"/>
    </form>
  </div>
</template>

<script>
import Button from "primevue/button";
import InputText from "primevue/inputtext";

import api from "@/api/auth.js"

export default {
  components: {
    Button,
    InputText,
  },
  data() {
    return {
      username: "",
      password: "",
    };
  },
  
  methods: {
 loginSubmit() {
  console.log(this.username);
  console.log(this.password);
      if (this.username === "" || this.password === "") {
        return;
      }
      
      api.login(this).catch(e =>{
        if(e.response.status === 404 ){
          alert(`Invalid data!`)
        }
      }).then(d=>{
        console.log(d);
        this.$router.push('/');
      })
    },
  },
  
};
</script>

<style>
.loginContainer {
  display: table;
  margin: 10% auto;
  background-color: var(--surface-card);
  padding: 30px;
  border-radius: 25px;
}
</style>
